// récupérer données dans le Local Storage pour alimenter le store

export const GET_WALLET_STORAGE = "GET_WALLET_STORAGE";
export const ADD_WALLET_STORAGE = "ADD_WALLET_STORAGE";
export const EDIT_WALLET_STORAGE = "EDIT_WALLET_STORAGE";
export const DELETE_WALLET_STORAGE = "DELETE_WALLET_STORAGE";

const priceFormater = (num) => {
  if (Math.round(num).toString().length < 4) {
    return new Intl.NumberFormat("us-US", {
      minimumFractionDigits: 2,
      maximumFractionDigits: 7,
    }).format(num);
  } else {
    return num;
  }
};

export const getWalletStorage = () => {
  console.log("action GET_WALLET_STORAGE");
  let arrayStorage = [];

  window.localStorage.walletStorage
    ? (arrayStorage = [...JSON.parse(window.localStorage.walletStorage)])
    : (arrayStorage = []);

  return (dispatch) => {
    dispatch({
      type: GET_WALLET_STORAGE,
      payload: arrayStorage,
    });
  };
};

// mettre à jour Local Storage et Store lorsqu'il y a des modifs
export const addWalletStorage = (coin) => {
  // export const addWalletStorage = (id) => {
  let objWallet = {};

  // objWallet.id = id;
  objWallet.id = coin.id;
  objWallet.ptf = "";
  objWallet.dateachat = "";
  objWallet.nbachat = 0;
  objWallet.pxachat = 0.0;
  objWallet.image = coin.image;
  objWallet.name = coin.name;

  // selon si on arrive par duplication dans Wallet ou depuis AppSuivi
  if (coin.cotation) {
    objWallet.cotation = coin.cotation;
  } else {
    objWallet.cotation = priceFormater(coin.current_price).toLocaleString();
  }

  console.log("ajout d'un coin : ");
  console.log(objWallet);

  let data = objWallet;

  let arrayStorage = [];

  window.localStorage.walletStorage
    ? (arrayStorage = [...JSON.parse(window.localStorage.walletStorage), data])
    : (arrayStorage = [data]);

  console.log("action ADD_WALLET_STORAGE");
  console.log("data objWallet à ajouter : ");
  console.log(data);

  console.log("arrayStorage : ");
  console.log(arrayStorage);

  return (dispatch) => {
    dispatch({
      type: ADD_WALLET_STORAGE,
      payload: arrayStorage,
    });

    window.localStorage.walletStorage = JSON.stringify(arrayStorage);
  };
};

// mettre à jour Local Storage et Store lorsqu'il y a des inputs sur lignes vides editWalletStorage  (et ajout si pleines addWalletStorage )

export const editWalletStorage = (objWallet, index) => {
  console.log("maj d'un coin sur ligne vide: ");
  console.log("objWallet : ");
  console.log(objWallet);
  console.log("index : ");
  console.log(index);

  let data = objWallet;

  let arrayStorage = JSON.parse(window.localStorage.walletStorage);
  let arrayStorageNew = [];
  console.log("arrayStorage avant filtre : ");
  console.log(arrayStorage);
  // on filtre arrayStorage : on enlève la ligne à mettre à jour pour la remettre version modifiée
  // splice enlève du tableau d'origine
  // le tableau résultant correspond à ce qui est enlevé

  arrayStorageNew = arrayStorage.splice(index, 1);
  console.log("arrayStorage après filtre : ");
  console.log(arrayStorage);

  arrayStorage = [...arrayStorage, data];

  console.log("arrayStorage après ajout actualisé: ");
  console.log(arrayStorage);

  console.log("action.payload EDIT_WALLET_STORAGE : ");

  return (dispatch) => {
    dispatch({
      type: EDIT_WALLET_STORAGE,
      payload: { ...data },
    });

    // console.log(payload);
    console.log("maj Local Storage par EDIT_WALLET_STORAGE");

    window.localStorage.walletStorage = JSON.stringify(arrayStorage);
  };
};

export const deleteWalletStorage = (index) => {
  console.log("delete de l'index: ");
  console.log(index);

  let arrayStorage = JSON.parse(window.localStorage.walletStorage);
  let arrayStorageNew = [];
  console.log("arrayStorage avant filtre : ");
  console.log(arrayStorage);

  arrayStorageNew = arrayStorage.splice(index, 1);
  console.log("arrayStorage après filtre : ");
  console.log(arrayStorage);

  return (dispatch) => {
    dispatch({
      type: DELETE_WALLET_STORAGE,
      payload: index,
    });

    console.log("maj Local Storage par DELETE_WALLET_STORAGE");

    window.localStorage.walletStorage = JSON.stringify(arrayStorage);
  };
};
