import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { addWalletStorage } from "../actions/wallet.action";

const WalletIcon = ({ coinId, coin }) => {
  const [wallet, setWallet] = useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    // if (window.localStorage.walletStorage) {
    // let walList = [];

    // for (let i = 0; i < window.localStorage.walletStorage.length; i++) {
    //   console.log("window.localStorage.walletStorage[i].id");
    //   console.log(window.localStorage.walletStorage[i].id);
    //   walList.push(window.localStorage.walletStorage[i].id);
    // }
    // console.log("walList");
    // console.log(walList);

    //   if (walList.includes(coinId)) {
    //     setWallet(true);
    //   } else {
    //     setWallet(false);
    //   }
    // }

    if (window.localStorage.coinWallet) {
      let walList = window.localStorage.coinWallet.split(",");

      if (walList.includes(coinId)) {
        setWallet(true);
      } else {
        setWallet(false);
      }
    }
  }, [coinId]);

  // Appel de la fonction au clic sur l'icône
  // si le token est déjà dans la liste, on l'enlève
  // si le token n'est pas dans la liste, on l'ajoute

  const idCheckerWallet = (id) => {
    let walList = null;

    if (window.localStorage.coinWallet) {
      walList = window.localStorage.coinWallet.split(",");
    }

    if (walList) {
      if (walList.includes(id)) {
        window.localStorage.coinWallet = walList.filter((coin) => coin != id);
        setWallet(false);
      } else {
        window.localStorage.coinWallet = [...walList, coinId];
        setWallet(true);
        // ajout d'un nouveau coin dans le wallet, ligne à vide
        // appel de l'action
        window.localStorage.walletStorage.includes(id)
          ? null
          : dispatch(addWalletStorage(coin));
      }
    } else {
      window.localStorage.coinWallet = coinId;
      setWallet(true);
      console.log("1er élément de local storage");
      // ajout d'un nouveau coin dans le wallet, ligne à vide
      // appel de l'action
      dispatch(addWalletStorage(coin));
    }
  };

  return (
    <img
      onClick={() => idCheckerWallet(coinId)}
      src={wallet ? "./assets/wallet-colored.svg" : "./assets/wallet-white.svg"}
      alt="icon-wallet"
    />
  );
};

export default WalletIcon;
