export const TOTAUX_WALLET_STORAGE = "TOTAUX_WALLET_STORAGE";

//--------------------------------
// String vers Number, sans séparateur de milliers, avec . au lieu de ,
//--------------------------------
function numberText(val) {
  val = val.replace(/\s/g, "");
  val = val.replace(/,/, ".");
  let valNumber = Number(val);
  return valNumber;
}

export const totauxWalletStorage = () => {
  console.log("action TOTAUX_WALLET_STORAGE");

  let arrayStorage = [];
  let arrayStorageTot = [];
  let objTotaux = {};

  window.localStorage.walletStorage
    ? (arrayStorage = [...JSON.parse(window.localStorage.walletStorage)])
    : (arrayStorage = []);

  console.log("length : " + arrayStorage.length);

  window.localStorage.totauxStorage
    ? (arrayStorageTot = [JSON.parse(window.localStorage.totauxStorage)])
    : (arrayStorageTot = []);

  objTotaux.totachat = 0;
  objTotaux.totvalo = 0;

  if (window.localStorage.walletStorage) {
    for (let i = 0; i < arrayStorage.length; i++) {
      objTotaux.totachat += arrayStorage[i].pxachat * arrayStorage[i].nbachat;
      objTotaux.totvalo +=
        numberText(arrayStorage[i].cotation) * arrayStorage[i].nbachat;
    }
  }

  return (dispatch) => {
    dispatch({
      type: TOTAUX_WALLET_STORAGE,
      payload: objTotaux,
    });
    window.localStorage.totauxStorage = JSON.stringify(objTotaux);
  };
};
