import {
  ADD_WALLET_STORAGE,
  GET_WALLET_STORAGE,
  EDIT_WALLET_STORAGE,
  DELETE_WALLET_STORAGE,
} from "../actions/wallet.action";

// le reducer met à jour le Store

const initialState = {};

export default function walletReducer(state = initialState, action) {
  console.log("REDUCER WALLET : " + action.type);
  switch (action.type) {
    case GET_WALLET_STORAGE:
      // maj du store avec local storage
      console.log("walletReducer get payload");
      console.log(action.payload);
      return action.payload;

    case ADD_WALLET_STORAGE:
      // maj du store avec local storage
      console.log("walletReducer add payload");
      return action.payload;

    case EDIT_WALLET_STORAGE:
      // maj du store avec input
      console.log("walletReducer edit input");

      return state.map((wallet, index) => {
        console.log("wallet.id : " + wallet.id);
        console.log("index : " + index);
        console.log("action.payload : ");
        console.log(action.payload);

        if (index === action.payload.index) {
          return {
            ...wallet,
            ptf: action.payload.ptf,
            dateachat: action.payload.dateachat,
            nbachat: action.payload.nbachat,
            pxachat: action.payload.pxachat,
          };
        } else {
          return wallet;
        }
      });

    case DELETE_WALLET_STORAGE:
      // delete ds le store avec input
      console.log("walletReducer delete");

      return state.filter((wallet) => wallet.id !== action.payload.id);

    default:
      return state;
  }
}
