import axios from "axios";
import React, { useEffect, useState } from "react";
import WalletTable from "../components/WalletTable";
import WalTableFilters from "../components/WalTableFilters";
import ToTop from "../components/ToTop";

const Wallet = () => {
  // Toutes les données de l'API
  const [coinsData, setCoinsData] = useState([]);

  useEffect(() => {
    axios
      .get(
        "https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=250&page=1&sparkline=false&price_change_percentage=1h%2C24h%2C7d%2C14d%2C30d%2C200d%2C1y"
      )
      .then((res) => setCoinsData(res.data));

    window.addEventListener("scroll", () => {
      if (window.scrollY > 145) {
        document.querySelector(".table-header-wallet").classList.add("active");
      } else {
        document
          .querySelector(".table-header-wallet")
          .classList.remove("active");
      }
    });
  }, []);

  return (
    <div className="app-container">
      <header>
        <h1>PORTEFEUILLE</h1>
        <WalTableFilters />
      </header>
      <WalletTable coinsData={coinsData} />
      <ToTop />
    </div>
  );
};

export default Wallet;
