import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import "./styles/index.scss";

// Redux
import { Provider } from "react-redux";
import { applyMiddleware, createStore } from "redux";
import { composeWithDevTools } from "redux-devtools-extension";
import thunk from "redux-thunk";
import rootReducer from "./reducers";
import { getWalletStorage } from "./actions/wallet.action";
import { totauxWalletStorage } from "./actions/totaux.action";

const root = ReactDOM.createRoot(document.getElementById("root"));
//Redux
const store = createStore(
  rootReducer,
  composeWithDevTools(applyMiddleware(thunk))
);

// On appelle l'action d'aller chercher les infos dès l'ouverture de la page

store.dispatch(getWalletStorage());
store.dispatch(totauxWalletStorage());

root.render(
  <Provider store={store}>
    <App />
  </Provider>
);
