import { TOTAUX_WALLET_STORAGE } from "../actions/totaux.action";

// le reducer met à jour le Store

const initialState = {};

export default function totauxReducer(state = initialState, action) {
  console.log("REDUCER TOTAUX : " + action.type);
  switch (action.type) {
    case TOTAUX_WALLET_STORAGE:
      console.log("walletReducer calcul des totaux");
      console.log(action.payload);
      return action.payload;

    default:
      return state;
  }
}
