import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  addWalletStorage,
  editWalletStorage,
  getWalletStorage,
  deleteWalletStorage,
} from "../actions/wallet.action";

import { totauxWalletStorage } from "../actions/totaux.action";

import StarIcon from "./StarIcon";
import WalletIcon from "./WalletIcon";
import sortcoinReducer from "../reducers/sortcoin.reducer";
import sortvaloReducer from "../reducers/sortvalo.reducer";
import sortptfReducer from "../reducers/sortptf.reducer";
// import storebtnReducer from "../reducers/storebtn.reducer";
// import { setStorebtnState } from "../actions/storebtn.action";

const TableLineWallet = ({ coin, index }) => {
  // let totalM1 = p1 * nb1;
  // console.log(totalM1);
  // const [editToggle, setEditToggle] = useState(true);
  console.log("TableLineWallet index : " + index);

  const [storePtf, setStorePtf] = useState("");
  const [storeDate, setStoreDate] = useState("");
  const [storeNb, setStoreNb] = useState(0);
  const [storePx, setStorePx] = useState(0);
  const [storeBtn, setStoreBtn] = useState("");
  const [storeIndicOn, setStoreIndicOn] = useState(false);

  const walletStore = useSelector((state) => state.walletReducer);
  const sortCoin = useSelector((state) => sortcoinReducer);
  const sortValo = useSelector((state) => sortvaloReducer);
  const sortPtf = useSelector((state) => sortptfReducer);
  // const storeIndicBtn = useSelector((state) => storebtnReducer);

  const dispatch = useDispatch();

  // const totauxStore = useSelector((state) => state.totauxReducer);

  let objetWallet = {
    // index: index,
    coinid: "",
    ptf: "",
    initdate: "",
    initnb: "",
    initpx: "",
    image: "",
    name: "",
    cotation: 0,
  };
  let arrayWallet = [];
  let arrayObjet = [];

  const priceFormater = (num) => {
    if (Math.round(num).toString().length < 4) {
      return new Intl.NumberFormat("us-US", {
        minimumFractionDigits: 2,
        maximumFractionDigits: 7,
      }).format(num);
    } else {
      return num;
    }
  };

  //--------------------------------
  // Renseignement State de l'input
  //--------------------------------
  function ActionInput(e) {
    e.preventDefault();

    switch (e.target.id) {
      case "ptf":
        console.log("ActionInput() modif ptf");
        console.log("storeIndicOn modif? : " + storeIndicOn);
        setStorePtf(e.target.value);
        if (storeIndicOn) {
          setStoreDate(coin.dateachat);
          setStoreNb(coin.nbachat);
          setStorePx(coin.pxachat);
        }
        break;
      case "initdate":
        setStoreDate(e.target.value);
        console.log("ActionInput() modif date");
        console.log("storeIndicOn modif? : " + storeIndicOn);
        if (storeIndicOn) {
          setStorePtf(coin.ptf);
          setStoreNb(coin.nbachat);
          setStorePx(coin.pxachat);
        }
        break;
      case "initnb":
        setStoreNb(e.target.value);
        console.log("ActionInput() modif nb");
        console.log("storeIndicOn modif? : " + storeIndicOn);
        if (storeIndicOn) {
          setStorePtf(coin.ptf);
          setStoreDate(coin.dateachat);
          setStorePx(coin.pxachat);
        }
        break;
      case "initpx":
        setStorePx(e.target.value);
        console.log("ActionInput() modif px");
        console.log("storeIndicOn modif? : " + storeIndicOn);
        if (storeIndicOn) {
          setStorePtf(coin.ptf);
          setStoreDate(coin.dateachat);
          setStoreNb(coin.nbachat);
        }

        break;
      default:
        null;
    }
  }

  //--------------------------------
  // Détermination action demandée
  //--------------------------------
  function ActionBtn(e) {
    // enlevé suite pb rechargement input avec valeurs
    // voir si on peut remettre apres correction bug devTools ?
    // e.preventDefault();
    console.log("ActionBtn () + " + e.target.id);
    console.log("StorePtf : " + storePtf);
    console.log("StoreDate : " + storeDate);
    console.log("StoreNb : " + storeNb);
    console.log("StorePx : " + storePx);
    // console.log(e.target.id);
    setStoreBtn(e.target.id);
  }
  //--------------------------------
  // Préparation de l'objet
  //--------------------------------
  function prepObjWallet(coin, index) {
    // Constitution de l'objet
    console.log("Fonction prepObjWallet() : ");
    console.log("coin avant: ");
    console.log(coin);
    objetWallet = {
      // index: index,
      id: coin.id,
      ptf: storePtf,
      dateachat: storeDate,
      nbachat: storeNb,
      pxachat: storePx,
      image: coin.image,
      name: coin.name,
      cotation: coin.cotation,
    };
    console.log("objetWallet après : ");
    console.log(objetWallet);
  }
  //--------------------------------
  // Réinitialisation des input après prise en compte des modifs
  //--------------------------------

  function reinitInput() {
    console.log(
      "inputs à réinitialiser: " +
        storePtf +
        " / " +
        storeDate +
        " / " +
        storePx +
        " / " +
        storeNb
    );
    setStorePtf("");
    setStoreDate("");
    setStorePx(0);
    setStoreNb(0);
  }

  //--------------------------------
  // String vers Number, sans séparateur de milliers, avec . au lieu de ,
  //--------------------------------
  function numberText(val) {
    val = val.replace(/\s/g, "");
    val = val.replace(/,/, ".");
    let valNumber = Number(val);
    return valNumber;
  }

  //--------------------------------
  // Fonction qui détecte le dernier token supprimé
  // MAJ indicateur pour suppression dans le coinWallet de Local Storage
  //--------------------------------
  function testLast(id) {
    let walList = window.localStorage.coinWallet.split(",");
    if (!walletStore.includes(id)) {
      window.localStorage.coinWallet = walList.filter((coin) => coin != id);
      console.log("suppression du token de coinWallet");
    }
  }

  useEffect(() => {
    switch (storeBtn) {
      case "add":
        console.log("switch StoreBtn Add : " + storeBtn);
        console.log("storeIndicOn : " + storeIndicOn);
        console.log("coin");
        console.log(coin);
        console.log("index : " + index);
        prepObjWallet(coin, index);

        dispatch(editWalletStorage(objetWallet, index));

        dispatch(getWalletStorage());

        dispatch(totauxWalletStorage());
        storeIndicOn ? setStoreIndicOn(false) : null;
        setStoreBtn("");
        // window.localStorage.walletStorage = JSON.stringify(walletStore);
        // dispatch(setStorebtnState(false));

        break;
      case "updt":
        console.log("switch StoreBtn Modif idem Add : " + storeBtn);
        console.log("storeIndicOn : " + storeIndicOn);
        console.log("coin");
        console.log(coin);
        console.log("index : " + index);
        prepObjWallet(coin, index);

        dispatch(editWalletStorage(objetWallet, index));

        dispatch(getWalletStorage());

        dispatch(totauxWalletStorage());
        storeIndicOn ? setStoreIndicOn(false) : null;
        setStoreBtn("");
        // window.localStorage.walletStorage = JSON.stringify(walletStore);
        // dispatch(setStorebtnState(false));

        break;
      case "del":
        console.log("switch StoreBtn Suppr : " + storeBtn);
        console.log("index : " + index);
        dispatch(deleteWalletStorage(index));

        dispatch(getWalletStorage());

        dispatch(totauxWalletStorage());
        setStoreBtn("");
        testLast(coin.id);

        // window.localStorage.walletStorage = JSON.stringify(walletStore);
        // dispatch(setStorebtnState(false));
        break;
      case "add-off":
        console.log("switch StoreBtn Add-off : " + storeBtn);
        console.log("coin");
        console.log(coin);
        console.log("index : " + index);
        dispatch(addWalletStorage(coin));

        dispatch(getWalletStorage());

        dispatch(totauxWalletStorage());
        setStoreBtn("");
        // window.localStorage.walletStorage = JSON.stringify(walletStore);
        // dispatch(setStorebtnState(false));
        break;
      case "updt-off":
        console.log("switch StoreBtn Modif-off : " + storeBtn);
        setStoreIndicOn(true);

        setStorePtf(coin.ptf);
        setStoreDate(coin.dateachat);
        setStoreNb(coin.nbachat);
        setStorePx(coin.pxachat);

        // dispatch(getWalletStorage());
        // dispatch(totauxWalletStorage());
        setStoreBtn("");
        // dispatch(setStorebtnState(false));
        break;
      case "del-off":
        console.log("switch StoreBtn Suppr-off : " + storeBtn);
        console.log("index : " + index);
        dispatch(deleteWalletStorage(index));

        dispatch(getWalletStorage());
        // window.localStorage.walletStorage = JSON.stringify(walletStore);
        dispatch(totauxWalletStorage());
        setStoreBtn("");
        testLast(coin.id);
        // dispatch(setStorebtnState(false));
        break;

      default:
        null;
    }
    console.log("fin des modif, maj LS");
    window.localStorage.walletStorage = JSON.stringify(walletStore);
  }, [storeBtn]);

  return (
    <div className="table-line-wallet">
      <div className="infos-container-wallet">
        {/* <StarIcon coinId={coin.id} />
        <WalletIcon coinId={coin.Id} coin={coin} /> */}

        <p>{index + 1}</p>
        <div className="img">
          <img src={coin.image} height="20" alt="logo" />
        </div>
        <div className="infos-wallet">
          <h4>{coin.name}</h4>
          {/* <span>- {coin.symbol.toUpperCase()}</span> */}
          <a
            target="_blank"
            href={
              "https://www.coingecko.com/fr/pi%C3%A8ces/" +
              coin.name
                .toLowerCase()
                .replace(" ", "-")
                .replace(" ", "-")
                .replace(" ", "-")
            }
          >
            <img src="./assets/info-icon.svg" alt="info-icon" />
          </a>
        </div>
      </div>
      <div className="form-container">
        {coin.pxachat === 0 || storeIndicOn ? (
          <form action="">
            <input
              type="text"
              name="plateforme"
              id="ptf"
              onChange={ActionInput}
              autoComplete="off"
              placeholder={storeIndicOn ? coin.ptf : ""}
            />
            <input
              type="date"
              name="initdate"
              id="initdate"
              // value={coin.dateachat}
              onChange={ActionInput}
              value={storeIndicOn ? coin.date : undefined}
            />
            <input
              type="number"
              name="initnb"
              id="initnb"
              onChange={ActionInput}
              placeholder={storeIndicOn ? coin.nbachat : 0}
            />
            <input
              type="number"
              name="initpx"
              id="initpx"
              onChange={ActionInput}
              placeholder={storeIndicOn ? coin.pxachat : 0}
            />
            <button className="btn">
              <img
                src="./assets/addbtn.svg"
                alt="btn-add"
                id="add"
                onClick={ActionBtn}
              ></img>
            </button>
            <button className="btn">
              <img
                src="./assets/update.svg"
                alt="btn-update"
                id="updt"
                onClick={ActionBtn}
              />
            </button>
            <button className="btn">
              <img
                src="./assets/bin.svg"
                alt="btn-delete"
                id="del"
                onClick={ActionBtn}
              />
            </button>
          </form>
        ) : (
          <div className="esp-container">
            <p id="ptf">{coin.ptf}</p>
            <p id="initdate">{coin.dateachat}</p>
            <p id="initnb">{coin.nbachat}</p>
            <p id="initpx">{coin.pxachat}</p>
            <button className="btn">
              <img
                src="./assets/addbtn.svg"
                alt="btn-add"
                id="add-off"
                onClick={ActionBtn}
              ></img>
            </button>
            <button className="btn">
              <img
                src="./assets/update.svg"
                alt="btn-update"
                id="updt-off"
                onClick={ActionBtn}
              />
            </button>
            <button className="btn">
              <img
                src="./assets/bin.svg"
                alt="btn-delete"
                id="del-off"
                onClick={ActionBtn}
              />
            </button>
          </div>
        )}
      </div>
      <p id="Valoachat">{(coin.nbachat * coin.pxachat).toFixed(2)}</p>
      <p>{numberText(coin.cotation)}</p>
      <p id="Valoactu">
        {(coin.nbachat * numberText(coin.cotation)).toFixed(2)}
      </p>
      <p>
        {(
          coin.nbachat * numberText(coin.cotation) -
          coin.nbachat * coin.pxachat
        ).toFixed(4)}
      </p>
      <p>
        {isNaN(
          (
            ((coin.nbachat * numberText(coin.cotation) -
              coin.nbachat * coin.pxachat) *
              100) /
            (coin.nbachat * coin.pxachat)
          ).toFixed(4)
        )
          ? "0.00"
          : (
              ((coin.nbachat * numberText(coin.cotation) -
                coin.nbachat * coin.pxachat) *
                100) /
              (coin.nbachat * coin.pxachat)
            ).toFixed(4)}
      </p>
    </div>
  );
};

export default TableLineWallet;
