import { combineReducers } from "redux";
import stableReducer from "./stable.reducer";
import listReducer from "./list.reducer";
import walletReducer from "./wallet.reducer";
import totauxReducer from "./totaux.reducer";
import sortcoinReducer from "./sortcoin.reducer";
import sortvaloReducer from "./sortvalo.reducer";
import sortptfReducer from "./sortptf.reducer";
import storebtnReducer from "./storebtn.reducer";

export default combineReducers({
  stableReducer,
  listReducer,
  walletReducer,
  totauxReducer,
  sortcoinReducer,
  sortvaloReducer,
  sortptfReducer,
  storebtnReducer,
});
