import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setSortcoinState } from "../actions/sortcoin.action";
import { setSortptfState } from "../actions/sortptf.action";
import { setSortvaloState } from "../actions/sortvalo.action";
// import { setStorebtnState } from "../actions/storebtn.action";
import { getWalletStorage } from "../actions/wallet.action";
import TableLineWallet from "./TableLineWallet";
import ToTop from "./ToTop";
import { isEmpty } from "./Utils";

const WalletTable = ({ coinsData }) => {
  const walletStore = useSelector((state) => state.walletReducer);
  const totauxStore = useSelector((state) => state.totauxReducer);
  // const storeBtn = useSelector((state) => state.btnReducer);

  const [orderBy, setOrderBy] = useState("libtoken");
  const [convertData, setConvertData] = useState();

  const [sortCoin, setSortCoin] = useState(true);
  const [sortValo, setSortValo] = useState(false);
  const [sortPtf, setSortPtf] = useState(false);
  // const [storeIndicBtn, setStoreIndicBtn] = useState(false);

  // const sortCoin = useSelector((state) => state.sortcoinReducer);
  // const sortValo = useSelector((state) => state.sortvaloReducer);
  // const sortPtf = useSelector((state) => state.sortptfReducer);

  const dispatch = useDispatch();

  useEffect(() => {
    console.log("useEffect WalletTable sur sortCoin, sortValo, sortPtf : ");
    dispatch(setSortcoinState(sortCoin));
    dispatch(setSortvaloState(sortValo));
    dispatch(setSortptfState(sortPtf));

    window.localStorage.walletStorage = JSON.stringify(walletStore);
  }, [sortCoin, sortValo, sortPtf]);

  //--------------------------------
  // String vers Number, sans séparateur de milliers, avec . au lieu de ,
  //--------------------------------
  function numberText(val) {
    val = val.replace(/\s/g, "");
    val = val.replace(/,/, ".");
    let valNumber = Number(val);
    return valNumber;
  }

  function valo(cotation, nbachat) {
    return nbachat * numberText(cotation);
  }

  function synchrosort(sortX, sortvalue) {
    console.log("synchrosort : " + sortX + " / " + sortvalue);
    console.log("orderBy : " + orderBy);

    if (!sortvalue) {
      switch (sortX) {
        case "sortCoin":
          console.log("case sortCoin");
          setSortCoin(true);
          setOrderBy("libtoken");
          document.getElementById("libtoken").checked = true;
          setSortPtf(false);
          document.getElementById("libptf").checked = false;
          setSortValo(false);
          document.getElementById("libvalo").checked = false;
          break;

        case "sortPtf":
          console.log("case sortPtf");
          setSortPtf(true);
          setOrderBy("libptf");
          document.getElementById("libptf").checked = true;
          setSortCoin(false);
          document.getElementById("libtoken").checked = false;
          setSortValo(false);
          document.getElementById("libvalo").checked = false;
          break;

        case "sortValo":
          console.log("case sortValo");
          setSortValo(true);
          setOrderBy("libvalo");
          document.getElementById("libvalo").checked = true;
          setSortPtf(false);
          document.getElementById("libptf").checked = false;
          setSortCoin(false);
          document.getElementById("libtoken").checked = false;
          break;

        default:
          null;
      }
    } else {
      switch (sortX) {
        case "sortCoin":
          setSortCoin(false);
          setOrderBy("libtokenreverse");
          document.getElementById("libtoken").checked = true;
          break;

        case "sortPtf":
          setSortPtf(false);
          setOrderBy("libptfreverse");
          document.getElementById("libptf").checked = true;
          setSortCoin(false);
          break;

        case "sortValo":
          setSortValo(false);
          setOrderBy("libvaloreverse");
          document.getElementById("libvalo").checked = true;
          break;

        default:
          null;
      }
    }
    console.log(
      "result synchrosort Coin / Ptf / Valo : " +
        sortCoin +
        " / " +
        sortPtf +
        " / " +
        sortValo
    );
  }

  useEffect(() => {
    axios
      .get(
        "https://api.getgeoapi.com/v2/currency/convert?api_key=f3da8c0fcfe0fea7c08bc49095115cf1ffada4c8&from=USD&to=EUR&format=json"
      )
      .then((res) => setConvertData(res.data.rates.EUR.rate));
  }, []);

  return (
    <div className="table-container-wallet">
      <ul className="table-header-wallet">
        <div className="range-container-wallet">
          {/* <span>Tokens</span> */}
          <span className="lib">
            <input
              type="radio"
              id="libtoken"
              defaultChecked={
                orderBy === "libtoken" || orderBy === "libtokenreverse"
                  ? true
                  : false
              }
              onClick={() => synchrosort("sortCoin", sortCoin)}
            />
            <label htmlFor="libtoken">Tokens</label>
          </span>
          <ToTop />
        </div>
        {/* <li className="plateforme">Plateforme</li> */}
        <li className="lib">
          <input
            type="radio"
            id="libptf"
            defaultChecked={
              orderBy === "libptf" || orderBy === "libptfreverse" ? true : false
            }
            onClick={() => synchrosort("sortPtf", sortPtf)}
          />
          <label htmlFor="libptf">Plateforme</label>
        </li>
        <li className="initdate">Date achat</li>
        <li className="initnb">Nb</li>
        <li className="initpx">Px achat $</li>
        <li className="initvalue">Valo achat $</li>
        <li className="cotation">Cotation $</li>
        {/* <li className="valorisation">Valorisation $</li> */}
        <li className="lib">
          <input
            type="radio"
            id="libvalo"
            defaultChecked={
              orderBy === "libvalo" || orderBy === "libvaloreverse"
                ? true
                : false
            }
            onClick={() => synchrosort("sortValo", sortValo)}
          />
          <label htmlFor="libvalo">Valorisation $</label>
        </li>
        <li className="latentevalue">Value latente $</li>
        <li className="latentepercent">Value latente %</li>
      </ul>

      <div className="coin-container">
        {!isEmpty(walletStore) &&
          walletStore

            .sort((a, b) => {
              console.log("Tri map walletReducer orderBy : " + orderBy);

              switch (orderBy) {
                case "libtoken":
                  if (a.name < b.name) return -1;
                  else if (a.name == b.name) return 0;
                  else return 1;
                case "libtokenreverse":
                  if (a.name < b.name) return 1;
                  else if (a.name == b.name) return 0;
                  else return -1;

                case "libptf":
                  if (a.ptf < b.ptf) return -1;
                  else if (a.ptf == b.ptf) return 0;
                  else return 1;
                case "libptfreverse":
                  if (a.ptf < b.ptf) return 1;
                  else if (a.ptf == b.ptf) return 0;
                  else return -1;

                case "libvalo":
                  return (
                    valo(a.cotation, a.nbachat) - valo(b.cotation, b.nbachat)
                  );
                case "libvaloreverse":
                  return (
                    valo(b.cotation, b.nbachat) - valo(a.cotation, a.nbachat)
                  );
                default:
                  null;
              }
            })
            .map((coin, index) => (
              <TableLineWallet coin={coin} index={index} key={index} />
            ))}
      </div>
      <div className="table-line-totaux-dollars">
        <p>TOTAUX en $</p>
        <ul>
          <li className="totachat">{totauxStore.totachat.toFixed(2)} $</li>
          <li className="cotation">{}</li>
          <li className="totvalo">{totauxStore.totvalo.toFixed(2)} $</li>
          <li className="totlat">
            {(totauxStore.totachat - totauxStore.totvalo).toFixed(2)} $
          </li>
          <li className="totlatprct">
            {(totauxStore.totachat - totauxStore.totvalo) / totauxStore.totachat
              ? (
                  (totauxStore.totachat - totauxStore.totvalo) /
                  totauxStore.totachat
                ).toFixed(4)
              : 0}{" "}
            %
          </li>
        </ul>
      </div>
      <div className="table-line-totaux-euros">
        <p>TOTAUX en €</p>
        <ul>
          <li className="totachat">
            {(totauxStore.totachat * convertData).toFixed(2)} €
          </li>
          <li className="cotation">{}</li>
          <li className="totvalo">
            {(totauxStore.totvalo * convertData).toFixed(2)} €
          </li>
          <li className="totlat">
            {(
              (totauxStore.totachat - totauxStore.totvalo) *
              convertData
            ).toFixed(2)}{" "}
            €
          </li>
          <li className="totlatprct">
            {(totauxStore.totachat - totauxStore.totvalo) / totauxStore.totachat
              ? (
                  (totauxStore.totachat - totauxStore.totvalo) /
                  totauxStore.totachat
                ).toFixed(4)
              : 0}{" "}
            %
          </li>
        </ul>
      </div>
    </div>
  );
};

export default WalletTable;
